<template>
  <!-- 笔记列表页面 -->

  <div class="notesBox">
    <p class="title">
      <span>学习笔记 </span>
      <span class="artlength"> {{ articleLength }}</span>
    </p>

    <!-- 布局盒子 -->
    <div class="notes_layout">
      <!-- 滚动盒子 -->
      <h-scrollContainer @arrive="arrive">
        <ul class="notesList">
          <!-- item.name 是组件对象需要在获取里面name -->
          <li
            v-for="(item, index) in article"
            :key="item.id"
            @click="intoDetails(index, item.date, item.type)"
          >
            <p>{{ item.describe }}</p>
            <p>记录时间：{{ item.date }}</p>
          </li>
        </ul>
      </h-scrollContainer>
    </div>
  </div>
</template>
<script>
import { ref, defineComponent, reactive, computed } from "vue";
import { useRouter } from "vue-router";
// 导入文章列表
import { components } from "@/assets/article/index";
export default defineComponent({
  setup() {
    const router = useRouter();
    const article = reactive(components);
    const scrollbarRef = ref(null);
    // 计算文章数量Object.keys,返回对象属性名字组成的数组
    const articleLength = computed(() => Object.keys(components).length);
    // 滚动条触底或顶事件
    const arrive = function (tb) {
      console.log(tb);
    };
    // 点击文章，传递name进入详情页面
    const intoDetails = function (index, date, tag) {
      // url中显示参数跳转
      router.push({
        name: "details",
        query: { article: index, date: date, tag: tag },
      });
    };

    return {
      scrollbarRef,
      article,
      articleLength,
      intoDetails,
      arrive,
    };
  },
});
</script>
<style lang="less" scoped>
.notesBox {
  position: relative;
  height: 100%;
  .title {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 0 160px;
    box-sizing: border-box;
    line-height: 50px;
    font-style: italic;
    font-size: 20px;
    transition: @T;
    background: @AC;
    .artlength {
      margin-left: 20px;
    }
  }

  .notes_layout {
    height: 100%;
    padding-top: 50px;
    box-sizing: border-box;
    transition: @T;
    .notesList {
      padding: 0px 150px;
      margin-bottom: 50px;
      transition: @T;
      li {
        padding: 10px;
        border-bottom: 1px solid #ddd;
        p:nth-child(1) {
          font-size: 16px;
          line-height: 40px;
        }
        p:nth-child(2) {
          font-size: 12px;
          color: rgb(185, 185, 185);
        }
        &:hover {
          cursor: pointer;
          color: @TC;
        }
      }
    }
  }
}
.med {
  .title {
    padding: 0px 10px;
  }
  .notes_layout {
    .notesList {
      padding: 0px 10px;
    }
  }
}
.min {
  .title {
    font-size: 18px;
    line-height: 30px;
  }
  .notes_layout {
    padding-top: 30px;
    .notesList {
      li {
        padding: 5px 0px;
        p {
          &:nth-child(1) {
            line-height: 24px;
            font-size: 14px;
            overflow: hidden;
            text-overflow: ellipsis; /* 加省略号 */
            white-space: nowrap; /* 强制不换行 */
          }
          &:nth-child(2) {
            line-height: 18px;
            font-size: 12px;
          }
        }
      }
    }
  }
}
</style>
